<template>
  <section class="hero is-fullheight">
    <div class="hero-body">
      <div class="container">
        <img :src="selectedImage" width="100%">
        <br>
        <br>
        <div>
          <div class="cake">
            <div class="icing">
              <div class="line"></div>
              <div class="squiggle"></div>
              <div class="squiggle2"></div>
              <div class="squiggle3"></div>
            </div>
            <div class="candle"></div>
            <div class="flame"></div>
          </div>
          <div class="happybday">Happy birthday!</div>
          <div class="person">Chonnikan Fakhaw</div>
        </div>
      </div>
    </div>
  </section>
</template>

<style>
.person {
  font-size: 65px;
  color: #450a2a;
}

.cake {
  position: relative;
  margin: 0 auto;
  width: 35px;
  height: 18px;
  background-color: #f2d4cc;
  border-radius: 0px 0px 3px 3px;
}

.icing {
  position: absolute;
  top: -10px;
  width: 0;
  height: 0;
  border-left: 30px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 10px solid #eaa19d;
}

.squiggle {
  position: absolute;
  top: 8px;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #eaa19d;
}
.squiggle:before {
  content: "";
  position: absolute;
  top: 0px;
  left: -5px;
  width: 5px;
  height: 5px;
  background-color: #eaa19d;
  border-radius: 50%;
}
.squiggle:after {
  content: "";
  position: absolute;
  top: 0px;
  left: -10px;
  width: 5px;
  height: 5px;
  background-color: #eaa19d;
  border-radius: 50%;
}

.squiggle2 {
  position: absolute;
  top: 8px;
  left: -15px;
  width: 5px;
  height: 5px;
  background-color: #eaa19d;
  border-radius: 50%;
}
.squiggle2:before {
  content: "";
  position: absolute;
  top: 0px;
  left: -5px;
  width: 5px;
  height: 5px;
  background-color: #eaa19d;
  border-radius: 50%;
}
.squiggle2:after {
  content: "";
  position: absolute;
  top: 0px;
  left: -10px;
  width: 5px;
  height: 5px;
  background-color: #eaa19d;
  border-radius: 50%;
}

.squiggle3 {
  position: absolute;
  top: 10px;
  left: -30px;
  width: 5px;
  height: 2.5px;
  background-color: #eaa19d;
  border-radius: 0px 0px 2.5px 2.5px;
}

.line {
  position: absolute;
  top: 20px;
  left: -30px;
  width: 35px;
  height: 2px;
  background-color: #eaa19d;
}

.candle {
  position: absolute;
  top: -12px;
  left: 18px;
  width: 3px;
  height: 10px;
  background-color: white;
}

.flame {
  position: absolute;
  border-radius: 50%;
  top: -15px;
  left: 18px;
  width: 3px;
  height: 3px;
  background-color: #ffbe22;
}
</style>

<script>
export default {
  data() {
    return {
      images: [
        "https://i.imgur.com/chNgxy4.jpg",
        "https://i.imgur.com/7OPHefL.jpg",
        "https://i.imgur.com/sKHfy93.jpg",
        "https://i.imgur.com/rr6V6HT.jpg",
        "https://i.imgur.com/A8XaEdN.jpg",
        "https://i.imgur.com/5CZeSLF.jpg",
        "https://i.imgur.com/R4pi8Dc.jpg",
        "https://i.imgur.com/ySdBUDi.jpg",
        "https://i.imgur.com/KQG6OlV.jpg"
      ],
      selectedImage: ""
    };
  },
  created() {
    const idx = Math.floor(Math.random() * this.images.length);
    this.selectedImage = this.images[idx];
  }
};
</script>


