import Vue from 'vue'
import Router from 'vue-router'
import Hbd from '@/components/Hbd'
import About from '@/components/About'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/hbd',
      name: 'Hbd',
      component: Hbd
    }
  ]
})



